@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
.form {
	display: grid;
	grid-template-columns: 2fr 1fr;
	grid-column-gap: 1em;
	-webkit-column-gap: 1em;
	        column-gap: 1em;
	align-items: flex-end;
}

.input {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}

.tags {
	display: flex;
	align-items: stretch;
	justify-content: space-between;
	margin-bottom: 1em;
}

.tags-button {
	width: 30%;
}

.form label {
	display: block;
	margin-bottom: 1em;
}

textarea,
#tag-input,
#author {
	font-family: inherit;
	font-size: 1rem;
	padding: 1em;
	border: none;
	border-radius: 0.2em;
	color: inherit;
	background-color: #b0a99050;
	transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

#author {
	margin-bottom: 1em;
}

textarea {
	width: 100%;
	/* min-width: 500px; */
	height: 10em;
	resize: none;
}

#tag-input {
	width: 65%;
}

textarea:focus,
#tag-input:focus,
#author:focus {
	background-color: white;
	box-shadow: 2px 2px 5px 3px #b0a99050;
}

.tags-button,
button[type='submit'] {
	border: none;
	padding: 1em;
	color: white;
	font-family: inherit;
	font-size: inherit;
	border-radius: 0.2em;
	cursor: pointer;
	background-color: #202030;
	transition: background-color 0.3s;
}

button[type='submit'] {
	width: 100%;
}

.tags-button:hover,
button[type='submit']:hover,
.tags-button:focus,
button[type='submit']:focus {
	background-color: #39304a;
}

.tags-wrapper {
	margin-bottom: 1em;
	display: grid;
	justify-content: center;
	align-items: center;
	grid-gap: 0.2em;
	gap: 0.2em;
	grid-template-columns: repeat(auto-fit, minmax(45%, 1fr));
	grid-template-rows: auto;
}

.tag {
	padding: 0.5em;
	background-color: #b0a990;
	border-radius: 0.2em;
	text-align: center;
	overflow: hidden;
	font-weight: bold;
	cursor: pointer;
	position: relative;
	font-family: monospace;
}

.remove {
	display: none;
	position: absolute;
	font-weight: normal;
	color: white;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 1;
	background-color: #b0a990;
	font-family: 'Montserrat', 'sans-serif';
}

.tag:hover .remove {
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}
.success {
	margin-top: 1em;
	color: #b0a990;
}
.error {
	margin-top: 1em;
	color: rgb(202, 96, 77);
}

.loader {
	-webkit-animation: 2s linear 0s infinite running rotate;
	        animation: 2s linear 0s infinite running rotate;
}

@-webkit-keyframes rotate {
	100% {
		transform: rotate(360deg);
	}
}

@keyframes rotate {
	100% {
		transform: rotate(360deg);
	}
}

@media screen and (max-width: 1200px) {
	.form {
		grid-template-columns: 1fr;
	}
	.tags-label {
		margin: 1em 0;
	}
} ;

.intro {
	margin-bottom: 1em;
}

.paragraph {
	text-align: center;
	font-size: 1.5rem;
	font-weight: bolder;
}

.message {
	padding: 1em;
	color: #202030;
	font-weight: 500;
	letter-spacing: 0.1em;
	border: 1px solid #b0a990;
	border-radius: 0.2em;
	border-top: 6px solid #b0a990;
	background-color: white;
	display: flex;
	flex-direction: column;
}
.message > * {
	margin-bottom: 1em;
}

.author {
	margin-bottom: 0.6em;
	text-transform: capitalize;
}

.message > *:last-child {
	margin-top: auto;
	margin-bottom: 0;
}
.author,
.date {
	display: flex;
	color: #b0a990;
}

.author-icon,
.date-icon {
	margin-right: 0.4em;
}

.tag-link {
	padding: 0.2em 0.3em;
	text-align: center;
	text-decoration: none;
	color: white;
	cursor: pointer;
	font-family: monospace;
	background-color: rgb(32, 32, 48);
	border-radius: 0.2em;
	margin-right: 0.4em;
	margin-bottom: 0.4em;
	text-transform: lowercase;
	text-align: center;
	font-size: 0.9rem;
	transition: background-color 0.3s;
}

.tag-link:hover {
	background-color: #39304a;
}
.tag-link:last-child {
	margin-right: 0;
	margin-bottom: 1em;
}

.tags-section {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0;
	align-items: flex-start;
}

.messages {
	margin-bottom: 1em;
}
.leading {
	text-align: center;
	margin: 1.4em 0;
}

.cards {
	display: grid;
	grid-gap: 1em;
	gap: 1em;
	justify-content: center;
	grid-template-columns: repeat(auto-fit, 23vw);
}

.no-messages,
.error-message,
.loading-message {
	text-align: center;
	margin-top: 1em;
}

.error-message {
	color: rgb(202, 96, 77);
}

@media screen and (max-width: 1200px) {
	.cards {
		grid-template-columns: repeat(auto-fit, 42vw);
	}
}
@media screen and (max-width: 790px) {
	.cards {
		grid-template-columns: repeat(auto-fit, 90vw);
	}
}

.load-more {
	margin-bottom: 1em;
	display: flex;
	justify-content: center;
}

.load-more button {
	background-color: #202030;
	color: white;
	transition: background-color 0.3s;
	padding: 0.4em 1em;
	border-radius: 0.2em;
	border: none;
	cursor: pointer;
	font-family: inherit;
}

.load-more button:hover,
.load-more button:focus {
	background-color: #39304a;
}

.about p {
	margin-top: 1em;
	text-align: justify;
	letter-spacing: 0.1em;
}

.signature.domain {
	font-size: 1.2rem;
}

.back-home {
	display: inline-block;
	text-decoration: none;
	text-align: left;
	margin-bottom: 1em;
	color: #202030;
	font-size: 1.3rem;
	font-weight: bold;
	transition: color 0.3s;
}

.back-home:hover {
	color: #39304a;
}

.tagCont {
	text-align: center;
	margin: 1em 0;
}

.nav {
	width: 100vw;
	background-color: #202030;
	box-shadow: 0px 0px 5px 3px #20203041;
	position: fixed;
	z-index: 999;
	top: 0;
}

.navbar {
	display: flex;
	width: 90%;
	max-width: 1200px;
	list-style: none;
	justify-content: flex-end;
	margin: 0 auto;
}
.navbar li {
	margin-left: 2em;
	transition: background-color 0.3s ease-in-out;
}
.navbar li:first-child {
	margin-right: auto;
	margin-left: 0;
}

.navbar li > a {
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
	color: white;
	padding: 1em;
	font-weight: bold;
	letter-spacing: 0.2em;
	text-transform: uppercase;
}

.navbar li:hover,
.navbar li:focus {
	background-color: #39304a;
}

*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

:focus {
	outline: none;
}

body {
	font-size: 16px;
	color: #202030;
	font-family: 'Montserrat', 'sans-serif';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
main {
	width: 90vw;
	max-width: 1200px;
	margin: 0 auto;
	margin-top: 5em;
}
.page {
	margin: 0 1em;
}
::selection {
	background-color: #b0a990;
	color: #202030;
}

/* scrollbar */

/* width */
::-webkit-scrollbar {
	width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
	background: #b0a9906b;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #202030;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: #39304a;
}

@media screen and (max-width: 320px) {
	main {
		width: 100vw;
		margin-left: 0;
		margin-right: 0;
	}
}

